import React, { useState } from 'react';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { darcula } from 'react-syntax-highlighter/dist/esm/styles/prism';
import './Simulador.css';

const Simulador = () => {
    const [subcadena, setSubcadena] = useState("");
    const [texto, setTexto] = useState("");
    const [resultado, setResultado] = useState("");

    function preproceso(subcadena) {
        const alfabeto = new Array(256);
        for (let i = 0; i < 256; i++) {
            alfabeto[i] = subcadena.length;
        }
        for (let i = 0; i < subcadena.length - 1; i++) {
            alfabeto[subcadena.charCodeAt(i)] = subcadena.length - 1 - i;
        }
        return alfabeto;
    }

    function buscar(subcadena, texto) {
        const alfabeto = preproceso(subcadena);
        let saltar = 0;
        const posiciones = [];

        while (texto.length - saltar >= subcadena.length) {
            let i = 0;
            while (i < subcadena.length && subcadena[i] === texto[saltar + i]) {
                i++;
            }
            if (i === subcadena.length) {
                posiciones.push(saltar); 
            }
            saltar += alfabeto[texto.charCodeAt(saltar + subcadena.length - 1)];
        }

        return posiciones;
    }

    const handleBuscar = (e) => {
        e.preventDefault(); 
        const posiciones = buscar(subcadena, texto);
        if (posiciones.length > 0) {
            setResultado(`La subcadena se encontr ${posiciones.length} veces en las posiciones: ${posiciones.join(", ")}`);
        } else {
            setResultado("La subcadena no se encontr en el texto.");
        }
    };

    const codigo = `public class BoyerMooreHorspool {

    public static int[] preprocess(String subcadena) {

        int[] tablaDeSaltos = new int[256];

        for (int i = 0; i < 256; i++) {
            tablaDeSaltos[i] = subcadena.length();
        }

        for (int i = 0; i < subcadena.length() - 1; i++) {
           tablaDeSaltos[subcadena.charAt(i)] = subcadena.length() - 1 - i;
        }

        return tablaDeSaltos;
    }

    public static List<Integer> buscar(String subcadena, String texto) {
        
        int[] tablaDeSaltos = preprocess(subcadena);
        int saltar = 0;
        List<Integer> posiciones = new ArrayList<>();

        while (texto.length() - saltar >= subcadena.length()) {
            int i = 0;

            while (i < subcadena.length() && subcadena.charAt(i) == texto.charAt(saltar + i)) {
                i++;
            }

            if (i == subcadena.length()) {
                posiciones.add(saltar);
            }

            saltar = saltar + alfabeto[texto.charAt(saltar + subcadena.length() - 1)];
        }

        return posiciones;
    }
}`;

    return (
        <div className="simulador-container">
            <div className="simulador-form">
                <h2>Simulador de Cdigo</h2>
                <form onSubmit={handleBuscar}>
                    <div className="mb-3">
                        <label htmlFor="texto" className="simulador-label">Texto:</label>
                        <input id="texto" type="text" className="simulador-input" value={texto} onChange={(e) => setTexto(e.target.value)} />
                    </div>
                    <div className="mb-3">
                        <label htmlFor="subcadena" className="simulador-label">Subcadena:</label>
                        <input id="subcadena" type="text" className="simulador-input" value={subcadena} onChange={(e) => setSubcadena(e.target.value)} />
                    </div>
                    <button type="submit" className="simulador-button">Buscar</button>
                </form>
                <div className="simulador-result">
                    <label>Resultado: {resultado}</label>
                </div>
            </div>
            <div className="simulador-code">
                <h2>Cdigo Boyer-Moore-Horspool</h2>
                <SyntaxHighlighter language="java" style={darcula} customStyle={{ fontSize: '0.75em', maxHeight: '400px', overflow: 'auto' }}>
                    {codigo}
                </SyntaxHighlighter>
            </div>
        </div>
    );
};

export default Simulador;